.navbar {
  padding: 10px 0px; 
  display: flex;
  align-items: center;
  background-color:white; 
  height: 100px; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.navbar-brand img {
  width : 120px;
  height: 250px; 
}

.navbar-nav {
  display: flex;
  justify-content: center;
  flex-grow: 1;
}

.nav-item {
  margin: 0 30px; 
}

.nav-link {
  font-weight: 600 !important;
  color: black !important;
  font-style: normal ;
}

.nav-font {
  font-family: Lato;
  font-size: 18px;
  font-weight: 500 !important;
}

.nav-font:hover {
  color: #FF7D00;
}

.socialtop {
  display: flex;
  align-items: center;
  margin-left: 0%; 
}

.top-social a {
  padding: 1px; 
  color:  black;
  text-decoration: none;
}

.top-social a:hover {
  color: #FF7D00;
}

.carousel-control-next-icon:after {
  font-size: 45px;
  color: black;
}

.carousel-control-prev-icon:after {
  font-size: 45px;
  color: black;
}

.a:hover {
  color: #FF7D00;
}


@media (min-width: 769px) {
 
  .navbar-nav li {
    width: auto; 
    height: auto; 
  }
}


@media (max-width: 768px) {
  .navbar-nav {
    flex-direction: row; 
    justify-content: space-between; 
    align-items: center; 
    padding: 0; 
  }

  .nav-item {
    margin: 5px; 
  }

  .navbar-nav .social-icons {
    flex-wrap: wrap; 
    justify-content: center; 
    margin: 5px 0;
  }

  
  .navbar-nav li a {
    padding: 10px; 
  }

  
  .top-social a {
   padding-right: 5px; 
  }
}


@media (max-width: 768px) {
  .navbar-brand {
    margin-right: 0; 
    margin-left: 0; 
  }

  .navbar-brand img {
    display: block; 
  }
}
